#search-form {
  .search-box {
    font-family: $font-stack-body;
    color: $border;
    border: 0;
    margin: 0;
    padding: 5px;
    border: 2px solid $border;
    border-radius: 5px;
    outline: none;
    box-shadow: none;
    font-size: 18px;
    transition: border 0.3s;
    background: none;
  }

  .search-box:focus {
    outline: none;
    border-color: $gradient-one;
    box-shadow: none;
  }

  .search-button {
    background: none;
    border: none;
    color: $border;
  }
}
